nav {
  min-height: 10vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  button {
    background: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    border: 2px solid rbg(65, 65, 65);
    padding: 0.5rem;
    transition: all 0.3s ease;
    &:hover {
      background: #ff0079;
      color: white;
    }
  }
}

#logo {
  font-family: 'Pacifico', cursive;
  cursor: default;
}

@media screen and (max-width: 768px) {
  nav {
    button {
      z-index: 3;
    }
  }
} ;
